.side-bar-icons-main-container {
    z-index: 999;
    background-color: white;
    &.active {
        transform: translateX(100%);
    }
    &.inactive {
        transform: translateX(0);
    }
    transition: all 0.3s ease-in-out 0.1s;
    position: fixed;
    right: 0;
    top: 0px;
    margin-top: 78px;
    bottom: 0;
    overflow-y: hidden;
    .sidebar-iconsconyainer {
        display: flex;
        justify-content: space-around;
        flex-direction: column;
        padding-bottom: 100px;
        height: 100%;
        width: 10vw;
        div {
            margin: auto;
            font-size: 25px;
            color: #605E5E;
        }
        a {
            color: #605E5E;
        }
    }
}